 }  //         $transition-delay: $transition-delay + .25s

[data-anim="image-from-right"] {
  opacity: 0;
  transform: translateX(100px); }

[data-anim="image-from-left"] {
  opacity: 0;
  transform: translateX(-100px); }


[data-anim="line-from-left"] {
  transform: translateX(-100%); }

// [data-anim="image-from-bottom"]
//   opacity: 0
//   transform: translateY(100%)

[data-anim="image-from-left"],
[data-anim="image-from-right"] {
  @include sm {
    transform: translateY(50px);
    // transition: opacity $tr, visibility $tr, transform $tr
    // &.is-animate
    //   transform: translateY(0)
    //   opacity: 1
 } }    //   visibility: visible

[data-anim="splitter-fade"] {
  opacity: 0; }

[data-anim="from-bottom"] {
  transform: translateY(100%);
  opacity: 0;
  @include md {
    transform: translateY(150px); }
  @include sm {
    transform: translateY(75px); } }

[data-anim="contacts-from-bottom"] {
  transform: translateY(100px);
  opacity: 0; }

[data-anim="from-top"] {
  transform: translateY(-100%);
  opacity: 0;
  @include md {
    transform: translateY(150px); }
  @include sm {
    transform: translateY(75px); } }
